.div1 {
  float: left;
  width: 1065px;
  height: 342px;
}
.div1 .titleNode {
  width: 384px;
  height: 120px;
  /* float: left; */
  line-height: 103px;
  text-align: center;
  cursor: pointer;
  position: relative;
  /* margin-left: 88px; */
  font-size: 44px;
  text-shadow: 0 0 30px #f4faf5;
  color: #fff;
}
.div1 .realtime {
  margin-top: 41px;
  width: 1065px;
  height: 198px;
}
.div1 .realtime .bznum {
  float: left;
  position: relative;
  width: 225px;
  height: 198px;
}
.div1 .realtime .bznum p {
  position: absolute;
  left: 25%;
  font-size: 45px;
  letter-spacing: 3px;
  font-weight: 700;
  color: #00ffff;
}
.div1 .realtime .bznum p i {
  font-size: 32px;
  letter-spacing: 2px;
  color: #ffffff;
  font-style: normal;
  font-weight: normal;
}
.div1 .realtime .bznum span {
  margin: 38px 20px 0 30px;
  display: inline-block;
  width: 163px;
  height: 160px;
}
.div1 .realtime .bznum .peoplenum {
  text-align: center;
  width: 225px;
  height: 37px;
  position: absolute;
  top: 80%;
  font-size: 30px;
  letter-spacing: 2px;
  color: #ffffff;
}
